import {FC} from "react";
import "./Video.scss";
import {Carousel} from "react-responsive-carousel";
import {ReactComponent as RightArrow} from "../../../../icons/projectsRightArrow.svg";
import {ReactComponent as LeftArrow} from "../../../../icons/projectsLeftArrow.svg";
import Share from "../../../UI/share";
import Recommended from "../../../UI/recommended";

interface VideoProps {

}

const Video: FC<VideoProps> = ({}) => {
  return (
    <section className={'videoPage'}>
      <h2 className={'videoPage__heading'}>
        Волгоградским газовикам пригрозили миллионным штрафом за отказ от прямых договоров
      </h2>
      <section className={'videoPage__videoSlider videoSlider'}>
        <Carousel
          showIndicators={false}
          infiniteLoop={true}
          showStatus={false}
          swipeable={true}
          showThumbs={false}
          showArrows={true}
          autoPlay={false}
          dynamicHeight={true}
          centerMode={true}
          renderArrowNext={(onClickHandler, hasNext, label) =>
            hasNext && (
              <button type="button" onClick={onClickHandler} title={label} style={{
                position: 'absolute',
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                zIndex: 2,
                top: 'calc(50% - 15px)',
                width: '48px',
                height: '48px',
                cursor: 'pointer',
                backgroundColor: "rgba(0, 0, 0, 0.25)",
                border: 0,
                borderRadius: '8px',
                right: '12%' }}>
                <RightArrow/>
              </button>
            )}
          renderArrowPrev={(onClickHandler, hasPrev, label) =>
            hasPrev && (
              <button type="button" onClick={onClickHandler} title={label} style={{
                position: 'absolute',
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                zIndex: 2,
                top: 'calc(50% - 15px)',
                width: '48px',
                height: '48px',
                cursor: 'pointer',
                backgroundColor: "rgba(0, 0, 0, 0.25)",
                border: 0,
                borderRadius: '8px',
                left: '12%' }}>
                <LeftArrow/>
              </button>
            )}>
          <section style={{backgroundColor: 'green'}} className={'videoPage__video'}><h2>123123</h2></section>
          <section style={{backgroundColor: 'orange'}} className={'videoPage__video'}><h2>123123</h2></section>
          <section style={{backgroundColor: 'yellow'}} className={'videoPage__video'}><h2>123123</h2></section>
          <section style={{backgroundColor: 'green'}} className={'videoPage__video'}><h2>123123</h2></section>
          <section style={{backgroundColor: 'orange'}} className={'videoPage__video'}><h2>123123</h2></section>
          <section style={{backgroundColor: 'yellow'}} className={'videoPage__video'}><h2>123123</h2></section>
          <section style={{backgroundColor: 'green'}} className={'videoPage__video'}><h2>123123</h2></section>
          <section style={{backgroundColor: 'orange'}} className={'videoPage__video'}><h2>123123</h2></section>
          <section style={{backgroundColor: 'yellow'}} className={'videoPage__video'}><h2>123123</h2></section>
        </Carousel>
      </section>
      <section className={'videoPage__content content'}>
        <section className={'content__shareSection shareSection'}>
          <Share isLastAside={false} isBorderTop={false}/>
        </section>
        <section className={'content__recommended recommended'}>
          <Recommended
            color={'white'}
            heading={'Смотрите также'}/>
        </section>
      </section>
    </section>
  )
}

export default Video;