import {FC, useContext, useRef} from "react";
import "./Photo.scss";

import Mountains from "../../../../images/Mountains.png";
import Share from "../../../UI/share";
import Recommended from "../../../UI/recommended";
import {SideMenuContext} from "../../../../context/SideMenuContext";
import {ReactComponent as Cross} from "../../../../icons/cross.svg";
import {Carousel} from "react-responsive-carousel";
import {ReactComponent as RightArrow} from "../../../../icons/projectsRightArrow.svg";
import {ReactComponent as LeftArrow} from "../../../../icons/projectsLeftArrow.svg";

interface PhotoProps {

}

const Photo: FC<PhotoProps> = ({}) => {
  const imagePreviewRef = useRef(null);
  const { setTopMenuPoped, setTopMenuContent } = useContext<any>(SideMenuContext);

  const onImageClick = () => {
    setTopMenuContent(
      <>
        <div onClick={() => setTopMenuPoped(false)} className={'topMenu__close'}>
          <Cross/>
        </div>
        <section className={'topMenu__photoSlider videoSlider'}>
          <Carousel
            showIndicators={false}
            infiniteLoop={true}
            showStatus={false}
            swipeable={true}
            showThumbs={false}
            showArrows={true}
            autoPlay={false}
            dynamicHeight={true}
            centerMode={true}
            renderArrowNext={(onClickHandler, hasNext, label) =>
              hasNext && (
                <button type="button" onClick={onClickHandler} title={label} style={{
                  position: 'absolute',
                  display: 'flex',
                  justifyContent: 'center',
                  alignItems: 'center',
                  zIndex: 2,
                  top: 'calc(50% - 15px)',
                  width: '48px',
                  height: '48px',
                  cursor: 'pointer',
                  backgroundColor: "rgba(0, 0, 0, 0.25)",
                  border: 0,
                  borderRadius: '8px',
                  right: '12%' }}>
                  <RightArrow/>
                </button>
              )}
            renderArrowPrev={(onClickHandler, hasPrev, label) =>
              hasPrev && (
                <button type="button" onClick={onClickHandler} title={label} style={{
                  position: 'absolute',
                  display: 'flex',
                  justifyContent: 'center',
                  alignItems: 'center',
                  zIndex: 2,
                  top: 'calc(50% - 15px)',
                  width: '48px',
                  height: '48px',
                  cursor: 'pointer',
                  backgroundColor: "rgba(0, 0, 0, 0.25)",
                  border: 0,
                  borderRadius: '8px',
                  left: '12%' }}>
                  <LeftArrow/>
                </button>
              )}>
            <section style={{backgroundColor: 'green'}} className={'videoPage__video'}><h2>123123</h2></section>
            <section style={{backgroundColor: 'orange'}} className={'videoPage__video'}><h2>123123</h2></section>
            <section style={{backgroundColor: 'yellow'}} className={'videoPage__video'}><h2>123123</h2></section>
            <section style={{backgroundColor: 'green'}} className={'videoPage__video'}><h2>123123</h2></section>
            <section style={{backgroundColor: 'orange'}} className={'videoPage__video'}><h2>123123</h2></section>
            <section style={{backgroundColor: 'yellow'}} className={'videoPage__video'}><h2>123123</h2></section>
            <section style={{backgroundColor: 'green'}} className={'videoPage__video'}><h2>123123</h2></section>
            <section style={{backgroundColor: 'orange'}} className={'videoPage__video'}><h2>123123</h2></section>
            <section style={{backgroundColor: 'yellow'}} className={'videoPage__video'}><h2>123123</h2></section>
          </Carousel>
        </section>
      </>
    );
    setTopMenuPoped(true);
  }

  return (
    <section className={'photoPage'}>
      <section className={'photoPage__content content'}>
        <h2 className={'content__photosHeading'}>
          Бренд-шеф проектов Джейми Оливера для своих блюд использует Nemoloko
        </h2>
        <section className={'content__photosList photosList'}>
          <div
            onClick={onImageClick}
            className={'photosList__image'}>
            <img src={Mountains}/>
          </div>
          <div className={'photosList__image'}>
            <img src={Mountains}/>
          </div>
          <div className={'photosList__image'}>
            <img src={Mountains}/>
          </div>
          <div className={'photosList__image'}>
            <img src={Mountains}/>
          </div>
          <div className={'photosList__image'}>
            <img src={Mountains}/>
          </div>
          <div className={'photosList__image'}>
            <img src={Mountains}/>
          </div>
          <div className={'photosList__image'}>
            <img src={Mountains}/>
          </div>
          <div className={'photosList__image'}>
            <img src={Mountains}/>
          </div>
          <div className={'photosList__image'}>
            <img src={Mountains}/>
          </div>
          <div className={'photosList__image'}>
            <img src={Mountains}/>
          </div>
          <div className={'photosList__image'}>
            <img src={Mountains}/>
          </div>
          <div className={'photosList__image'}>
            <img src={Mountains}/>
          </div>
          <div className={'photosList__image'}>
            <img src={Mountains}/>
          </div>
          <div className={'photosList__image'}>
            <img src={Mountains}/>
          </div>
          <div className={'photosList__image'}>
            <img src={Mountains}/>
          </div>
        </section>
        <section className={'content__shareSection shareSection'}>
          <Share isLastAside={false} isBorderTop={false}/>
        </section>
        <section className={'content__recommended recommended'}>
          <Recommended
            color={'white'}
            heading={'Смотрите также'}/>
        </section>
      </section>
      <section
        ref={imagePreviewRef}
        className={'photoPage__photoPreview photoPreview'}>
        <section className={'photoPreview__outside outside'}/>
      </section>
    </section>
  )
}

export default Photo;