import {FC} from "react";
import "./SeparateComment.scss";
import {ISeparateComment} from "../../../interface";

const SeparateComment: FC<ISeparateComment> = ({author, date, text, depthLevel}) => {
  return (
    <div
      style={{
        marginLeft: (depthLevel - 1) * 36 + 'px'
      }}
      className={'separateComment'}>
      <div className={'separateComment__author author'}>
        <h3 className={'author__name'}>{author}</h3>
        <span className={'author__date'}>{date}</span>
      </div>
      <span className={'separateComment__text'}>
        {text}
      </span>
      <div className={'separateComment__buttons buttons'}>
        <span className={'buttons__item buttons__item_reply'}>Ответить</span>
        <span className={'buttons__item buttons__item_report'}>Пожаловаться</span>
      </div>
    </div>
  )
}

export default SeparateComment;